  color: var(--text-secondary, #666);
}

/*   */
.filter-summary {
  padding: 12px;
  background: var(--background-secondary, #f8f9fa);
  border-radius: 8px;
  border: 1px solid var(--border-color, #e0e0e0);
  flex-shrink: 0; /* Added */
}

.summary-title {
  font-weight: 600;
