  background: var(--primary-color, #4285f4);
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 600;
  box-shadow: 0 1px 4px rgba(66, 133, 244, 0.3);
}

.team-tag-name {
  font-weight: 600;
}

.team-tag-count {
  background: rgba(255, 255, 255, 0.2);
