  background: var(--background-hover, #f0f0f0);
  color: var(--text-secondary, #666);
  padding: 6px 12px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 500;
}

.no-teams-text {
  font-weight: 500;
}

/*   */
[data-theme="dark"] .advanced-filter {
  background: var(--dark-bg-primary, #1e1e1e);
  border: 1px solid var(--dark-border, #333);
}

[data-theme="dark"] .filter-title {
  color: var(--dark-text-primary, #ffffff);
}

[data-theme="dark"] .filter-controls {
  background: var(--dark-bg-secondary, #2a2a2a);
  border: 1px solid var(--dark-border, #333);
}

[data-theme="dark"] .global-checkbox-label {
