}

[data-theme="dark"] .reset-button {
  background: var(--dark-bg-hover, #404040);
  color: var(--dark-text-secondary, #b0b0b0);
  border: 1px solid var(--dark-border, #333);
}

[data-theme="dark"] .reset-button:hover:not(:disabled) {
  background: var(--dark-bg-accent, #505050);
  color: var(--dark-text-primary, #ffffff);
}

[data-theme="dark"] .team-item {
  background: var(--dark-bg-secondary, #2a2a2a);
  border: 1px solid var(--dark-border, #333);
}

[data-theme="dark"] .team-item.team-visible {
  border-color: var(--primary-color, #4285f4);
  box-shadow: 0 1px 4px rgba(66, 133, 244, 0.2);
}

[data-theme="dark"] .team-header {
  background: var(--dark-bg-primary, #1e1e1e);
  border-bottom: 1px solid var(--dark-border, #333);
}

[data-theme="dark"] .team-item.team-visible .team-header {
  background: rgba(66, 133, 244, 0.1);
}

[data-theme="dark"] .collapse-toggle {
  background: var(--dark-bg-secondary, #2a2a2a);
  border: 1px solid var(--dark-border, #333);
}

[data-theme="dark"] .collapse-toggle:hover {
  background: var(--dark-bg-hover, #404040);
}

[data-theme="dark"] .toggle-arrow {
  color: var(--dark-text-secondary, #b0b0b0);
}

[data-theme="dark"] .team-name {
  color: var(--dark-text-primary, #ffffff);
}

[data-theme="dark"] .team-count {
  color: var(--dark-text-secondary, #b0b0b0);
}

[data-theme="dark"] .employee-list {
  background: var(--dark-bg-secondary, #2a2a2a);
}

[data-theme="dark"] .employee-item {
  background: var(--dark-bg-primary, #1e1e1e);
}

[data-theme="dark"] .employee-item:hover {
  background: var(--dark-bg-hover, #404040);
  border-color: var(--dark-border, #333);
}

[data-theme="dark"] .employee-item.employee-visible {
  background: rgba(66, 133, 244, 0.1);
  border-color: rgba(66, 133, 244, 0.3);
}

[data-theme="dark"] .employee-item.employee-hidden {
  background: var(--dark-bg-secondary, #2a2a2a);
}

[data-theme="dark"] .employee-name {
  color: var(--dark-text-primary, #ffffff);
}

[data-theme="dark"] .employee-position {
  color: var(--dark-text-secondary, #b0b0b0);
  background: var(--dark-bg-hover, #404040);
}

[data-theme="dark"] .checkbox-design {
  background: var(--dark-bg-secondary, #2a2a2a);
  border: 2px solid var(--dark-border, #333);
}

[data-theme="dark"] .filter-summary {
  background: var(--dark-bg-secondary, #2a2a2a);
  border: 1px solid var(--dark-border, #333);
}

[data-theme="dark"] .summary-title {
  color: var(--dark-text-primary, #ffffff);
}

[data-theme="dark"] .no-teams {
  background: var(--dark-bg-hover, #404040);
  color: var(--dark-text-secondary, #b0b0b0);
}

[data-theme="dark"] .empty-title {
  color: var(--dark-text-primary, #ffffff);
}

[data-theme="dark"] .empty-message,
[data-theme="dark"] .empty-description {
  color: var(--dark-text-secondary, #b0b0b0);
}

/*  */
@media (max-width: 768px) {
  .advanced-filter {
    padding: 12px;
  }
  
  .filter-title-section {
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
  }
  
  .filter-controls {
    flex-direction: column;
    gap: 10px;
  }
  
  .team-header {
    padding: 10px;
  }
  
  .employee-list {
    padding: 10px;
  }
  
  .employee-item {
    padding: 6px;
  }
  
  .employee-avatar {
    width: 24px;
    height: 24px;
    font-size: 11px;
  }
  
  .visible-teams {
    justify-content: center;
  }
}